.contact-icons{
  margin-top: 150px;
  text-align: center;
}

@media (max-width: 1082px) {
  .contact-icons {
    width: 1082px;
  }
}

.contact-icons img{
  margin: 0 2em;
  animation: contact-icons 1s linear;
}

.contact-icons img:hover {
  transform: scale(1.1);
}

@keyframes contact-icons {
  0%{
    transform: translateY(-100px);
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}